
































export default {
	data: {
		telnumber: "",
		password: ""
	},
	//自动登陆
	onLoad: function() {
		var telnumber = uni.getStorageSync("telnumber");
		var password = uni.getStorageSync("password");
		if (telnumber.length != 0 && password.length != 0) {
			uni.showLoading({
				title: '登陆中',
				mask: 'true'
			});
			uni.request({
				url: 'http://148.70.148.17:8080/user/login',
				// url: 'http://192.168.191.1:8080/user/login',
				method: 'POST',
				data: {
					telnumber: telnumber,
					password: password
				},
				success: (res) => {
					console.log(res.data);
					if (res.data.ID == 0) {
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title: '登陆失败！'
						});
						return;
					} else {
						if (res.data.type == 0) {
							uni.setStorageSync('ID', res.data.ID);
							uni.setStorageSync('studentid', res.data.studentid);
							uni.setStorageSync('username', res.data.username);
							uni.setStorageSync('telnumber', telnumber);
							uni.setStorageSync('password', password);
							uni.hideLoading();
							uni.reLaunch({
								url: '../userinfor/userinfor'
							});
						}
						if (res.data.type == 1) {
							uni.setStorageSync('telnumber', telnumber);
							uni.setStorageSync('password', password);
							uni.hideLoading();
							uni.reLaunch({
								url: '../manager/manager'
							});
						}
					}
				},
				fail: (res) => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络服务异常！'
					});
				}
			})
		};
		uni.getProvider({
			service: 'push',
			success: function(res) {
				console.log(res.provider);
				// 个推的名称为 igexin
				if (~res.provider.indexOf('igexin')) {
					uni.subscribePush({
						provider: 'igexin',
						success: function(res) {
							console.log('success:' + JSON.stringify(res));
						}
					});
				}
			}
		});
	},

	methods: {
		//用于登陆
		bindLogin(telnumber, password) {
			if (login_null_test(telnumber, password) == 1)
				return;
			//登陆空值验证
			uni.showLoading({
				title: '登陆中',
				mask: 'true'
			});
			uni.request({
				url: 'http://148.70.148.17:8080/user/login',
				// url: 'http://192.168.191.1:8080/user/login',
				method: 'POST',
				data: {
					telnumber: telnumber,
					password: password
				},
				success: (res) => {
					console.log(res.data);
					if (res.data.ID == 0) {
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title: '登陆失败！'
						});
						return;
					}else {
						if (res.data.type == 0) {
							uni.setStorageSync('ID', res.data.ID);
							uni.setStorageSync('studentid', res.data.studentid);
							uni.setStorageSync('username', res.data.username);
							uni.setStorageSync('telnumber', telnumber);
							uni.setStorageSync('password', password);
							uni.hideLoading();
							uni.reLaunch({
								url: '../userinfor/userinfor'
							});
						}
						if (res.data.type == 1) {
							uni.setStorageSync('telnumber', telnumber);
							uni.setStorageSync('password', password);
							uni.hideLoading();
							uni.reLaunch({
								url: '../manager/manager'
							});
						}
					}
				},
				fail: (res) => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络服务异常！'
					});
				}
			});
		}
	}
}



function login_null_test(username, password) {
	//登陆空值验证
	if (username == '') {
		uni.showToast({
			icon: 'none',
			title: '账号不能为空'
		});
		return 1;
	}
	if (password == '') {
		uni.showToast({
			icon: 'none',
			title: '密码不能为空'
		});
		return 1;
	}
	return 0;
}
